.intro{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    /* background-image: url('../../assets/images/IMG_20220110_001919.jpg'); */
    background-color: #082032;
    background-repeat: no-repeat;
    background-size: cover;
}

@keyframes wind-shadow {
    from{
        text-shadow: 2px 2px #9ACD32;
    }
    to{
        text-shadow: 4px 4px #00FF00;
    }
    
}
.intro__introduction{
    font-family: Audiowide-Regular;
    width: 50vw;
    font-size: 2rem;
    text-align: left;
    /* animation:wind-shadow 2s alternate-reverse infinite; */
    color: var(--white-200);
    padding-left: 2%;
}

@keyframes rotate-360 {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

.intro__interstellar{
    width: 40vw;
    height: 40vw;
    animation: rotate-360;
    animation-duration: 36s;
    animation-iteration-count: infinite;
    
}

.align__center{
    display: flex;
    align-self: center;
    justify-content: center;
}

@media screen and (min-width:1920px) {
    .intro__introduction{
        font-size: 4rem;
    }
}

@media screen and (min-width: 1024px) and (max-width:1920px){
    .intro__introduction{
        font-size: 2.4rem;
    }
}

@media screen and (min-width: 768px) and (max-width:1024px){
    .intro__introduction{
        font-size: 2rem;
    }
}

@media screen and (max-device-width:768px){
    .intro__introduction{
        font-size: 3rem;
        width: 60vw;
    }    
}

@media screen and (max-device-width:426px){
    .intro__introduction{
        font-size:2rem;
        width: 80vw;
    }
}